.para-layer {
  padding-left: 20% !important;
  padding-right: 20% !important;
  padding: 0%; }

.spotlight {
  width: 100%;
  height: auto;
  position: relative; }

.flicker {
  -webkit-animation: flicker 5s alternate linear infinite;
  animation: flicker 5s alternate linear infinite; }

.flicker2 {
  -webkit-animation: flicker2 5s alternate linear infinite;
  animation: flicker2 5s alternate linear infinite; }

@keyframes flicker {
  0%, 19.999%, 22%, 62.999%, 64%, 68.999%, 70%, 100% {
    opacity: .99;
    text-shadow: -1px #000; }
  20%, 21.999%, 63%, 63.999%, 69%, 69.999% {
    opacity: 0;
    text-shadow: none; } }
@-webkit-keyframes flicker {
  0%, 19.999%, 22%, 62.999%, 64%, 68.999%, 70%, 100% {
    opacity: .99;
    text-shadow: -1px #000; }
  20%, 21.999%, 63%, 63.999%, 69%, 69.999% {
    opacity: 0;
    text-shadow: none; } }
@keyframes flicker2 {
  0%, 19.999%, 22%, 62.999%, 64%, 68.999%, 70%, 100% {
    opacity: 0;
    text-shadow: none; }
  20%, 21.999%, 63%, 63.999%, 69%, 69.999% {
    opacity: .99;
    text-shadow: -1px #000; } }
@-webkit-keyframes flicker2 {
  0%, 19.999%, 22%, 62.999%, 64%, 68.999%, 70%, 100% {
    opacity: 0;
    text-shadow: none; }
  20%, 21.999%, 63%, 63.999%, 69%, 69.999% {
    opacity: .99;
    text-shadow: -1px #000; } }



