.cell-layer {
  background-color: black;
  color: white;
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: 2;
  pointer-events: none;
  --gap-horizontal:20px;
  --gap-vertical:20px;
  --time-anim: 3s;
  --delay-anim: 1s;
  --blend-color-2: transparent;
  --blend-mode-2: none; }

#cell-center {
  width: 100%;
  height: 100%;
  position: relative; }

#cell-absolute {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 200px;
  width: 200px;
  margin-top: -200px;
  margin-left: -100px; }

.cell-image {
  position: absolute;
  width: 50%;
  left: 25%;
  top: 50%; }

#cell-image2 {
