  top: 67.2%;
  width: 42.1%;
  height: 63.5%;
  background-color: black; }

#cell-screen2 {
  position: absolute;
  left: 29.2%;
  top: 67.2%;
  width: 42.1%;
  height: 63.5%;
  background-color: black; }

.cell-normal {
